/* general */

body {
  font-family: "Nunito", Semi-bold;
  background-color: #ffffff !important;
  
}

h1 {
  font-size: 2.5em;
}

/* Partie 1 */

.partie1 {
  background-image: url("./fond.png");
  background-size: cover; /* <------ */
  background-repeat: no-repeat;
  background-position: center center;
}
.colUserMobile {
  display: none;
}

.logoPartie1 {
  text-align: center;
  padding-bottom: 11%;
  
}

.logoPartie1 img {
  width: 25%;
}




.colButton {
  text-align: right;
  margin-top: 0.5%;
}
.btn {
  background-color: transparent !important;
  border-color: white;
  border-radius: 28px;
}

.trigger {
  border-radius: 28px;
  background-color: #f5a624;
  padding: 2%;
  border-width: 0px;
  color: white;
  font-size: 18px;
  padding-left: 4%;
  padding-right: 4%;
  margin-bottom: 19%;
}

/* partie 2 services titres */

.partie2 {
  padding-top: 20%;
  z-index: 2;
}
.enSavoirPlus{
  border-radius: 28px;
  background-color: white;
  color:black!important;
  border-width: 1px;
    border-color: black;
   margin-bottom:3%;
  
}

.partie2Titre {
  text-align: center;
  color: #f5a624;
}

.partie2SousTitre {
  text-align: center;
}

/*mockups*/
.mockup1 {
  position: absolute;
  text-align: center;
  top: 30%;
    left: -6%;
  z-index: 1;
}
.mockup2 {
  position: absolute;
  text-align: center;
  top: 43%;
    left: 35%;
  z-index: 3;
}

.mockup1 img{
  width:20%
}
.mockup2 img{
  width:35%
}
/* partie 3 services blocs */

.partie3 {
  padding-top: 10%;
}

.partie3 p {
  padding-left: 17%;
  padding-right: 17%;
  margin-bottom:0%
}
.partie3Titre {
  text-align: center;
  color: #f5a624;
}

.clientBloc {
  text-align: center;
}

.serveurBloc {
  text-align: center;
}

.paraSer{
  font-size:18px;
}

.colorA{
  color: black;
}
.restaurateurBloc {
  text-align: center;
}

.clientBloc img {
  width: 12%;
  margin-bottom: 6%;
}

.serveurBloc img {
  width: 24%;
  margin-bottom: 7%;
}

.restaurateurBloc img {
  width: 22%;
  margin-bottom: 5%;
}

.video {
  width: 100%;
}

.popup-content {
  left: 0px !important;
}

/* partie 4 environnement*/

.partie4 {
  padding-top: 10%;
}

.partie4Titre {
  text-align: center;
  color: #f5a624;
}

.partie4 img {
  width: 14%;
  margin-top: 10%;
}
.partie4SousTitre {
  text-align: center;
}

/*partie 5 contact */

.partie5 {
  background-color: #f5a624;

  padding-bottom: 3%;
}

.partie5 img {
  width: 26%;
}

.partie5image {
  text-align: center;
  padding-top: 4%;
}

.partie5Contact {
  text-align: center;
  color: #ffffff;
  padding-top: 4%;
}

.adresseContact a {
  text-decoration: underline #c4c4c4;
  color: #c4c4c4 !important;
  font-size: 28px;
}

a {
  color: white !important;
  font-size: 28px;
}

.fab {
  color: #ffffff;
  margin: 12px;
  font-size: 37px;
  margin-right: 10px;
}

.copy {
  text-align: center;
  font-size: 10px;
}

.copy a {
  font-size: 18px;
}
.iconeFab {
  text-align: center;
}

.mockup1mobile {
  display: none;
}

.mockup1Ipad {
  display: none;
}

.mockup2Ipad {
  display: none;
}

@media (max-width: 640px) {
  /* CSS mobile ici */

  .logoPartie1 {
    padding-bottom: 3% !important;
  }

  .logoPartie1 img {
    margin-top: -9% !important;
    width: 50% !important;
  }

  .colUserMobile {
    display: block;
    text-align: right;
    margin-top: 1%;
  }
  .mockup1 {
    display: none;
  }

  

  .partie2 h1{
    font-size:1.8rem;
    
  }

  .partie2 h3{
    font-size: 1.6rem;
  }

  .mockup2 {
    display: none;
  }

  .logoPartie1 img {
    width: 63%;
    margin-top: 20%;
    padding-bottom: 0%;
  }
  .mockup1mobile {
    display: block !important;
   text-align: center;
  }

  .mockup1mobile img {
    width: 68%;
    padding-right: 10%;
  }

  .partie2{
    padding-top: 10%;
  }
  .colButton {
    display: none;
  }
  .partie5 {
    margin-bottom: 20%;
  }
  .clientBloc {
    margin-bottom: 20%;
  }
}

@media (min-width: 576px) and (max-width: 736px) {
  .mockup1 {
    display: none;
  }

  .mockup2 {
    display: none;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .colUserMobile {
    display: block;
    text-align: right;
    margin-top: 1%;
  }
  .mockup1 {
    display: none;
  }

  .mockup2 {
    display: none;
  }

  .mockup1Ipad {
    display: block;
    position: absolute;
    top: 13%;

    left: 19%;
  }

  .mockup1Ipad img {
    width: 119%;
  }

  .mockup2Ipad {
    display: block;
    position: absolute;
    top: 15%;
    left: 42%;
  }

  .mockup2Ipad img {
    width: 109%;
  }

  .colButton {
    display: none;
  }
}

/*ipad pro only*/

@media only screen and (min-device-width: 1366px) and (max-device-width: 1366px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 2) {
  .mockup1 img {
    width: 31%;
  }

  .mockup2 {
    top: 40%;
    left: 23%;
    width: 70%;
  }

  .mockup2 img {
    width: 41%;
  }
}
